@import 'src/common/styles/constants';
@import 'src/common/styles/mixins';

.RecordList {
  display: flex;
  justify-content: center;

  &__Content {
    padding: 0;
    margin: 0;

    &_withMinWith {
      min-width: 50%;
    }
  }

  &__ContentItem {
    display: flex;
    position: relative;
    justify-content: space-between;
    align-items: flex-end;

    &:after {
      z-index: 0;
      box-sizing: border-box;
      content: '';
      position: absolute;
      width: 100%;
      height: 0;
      line-height: 0;
      border-bottom: $dotted-border;
    }
  }

  &__ContentItemName {
    z-index: 1;
    background: $app-background-color;
  }

  &__ContentItemValue {
    z-index: 1;
    background: $app-background-color;
  }
}

@media (max-width: $md) {
  .RecordList {
    &__Content {
      &_withMinWith {
        min-width: 100%;
      }
    }
  }
}
